.card{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 13px 18px;
  border-radius: 16px;
  color: black;
  text-decoration: none;
  gap: 10px;

  &:active{
    opacity: 0.5;
  }

  &__tag {
    font-size: 26px;
  }
  &__eda {
    height: 16px;
  }
}